	module.exports = {"data":{"post":{"html":"<p>As <a href=\"http://news.scotsman.com/latest.cfm?id=3501484\" title=\"Scotsman.com News: Police Guard as Beslan's Children Return to School\">schools reopened today in Beslan</a> after the massacre two weeks ago, Thomas Nephew mentions <a href=\"http://pages.prodigy.net/thomasn528/blog/2004_09_12_newsarcv.html#109526652339568820\" title=\"newsrack blog\">a couple ways to help children in Beslan</a>.  </p>\n<p>1. <a href=\"http://www.moscowhelp.org/donate.htm\" title=\"Help Beslan children\">www.moscowhelp.org</a>, a group originally formed to help victims of the Moscow theatre attack, is attempting to raise $10 million for Beslan.</p>\n<p>2. <a href=\"http://oxblog.blogspot.com/2004_09_12_oxblog_archive.html#109524686354768401\">OxBlog</a> forwards an appeal for specific medical equipment that is needed.</p>\n<p>Here in Germany, the newsmagazine <a href=\"http://focus.msn.de/hps/fol/newsausgabe/newsausgabe.htm?id=6349\" title=\"Hier können Sie spenden! - Ausland - Politik - FOCUS Online\">Focus</a> lists organizations accepting donations for Beslan via <em>Banküberweisung</em>.</p>","fields":{"title":"Help Beslan children","date":"15 Sep 2004","url":"https://papascott.de/2004-09-15-help-beslan-children/"}}},"pathContext":{"slug":"/2004-09-15-help-beslan-children/","prev":{"fields":{"title":"Spoon-Feed or Fork-Feed?","date":"14 Sep 2004","slug":"/2004-09-14-spoon-feed-or-fork-feed/"}},"next":{"fields":{"title":"Offering Degrees in New German?","date":"15 Sep 2004","slug":"/2004-09-15-offering-degrees-in-new-german/"}}}}

